import React from 'react';

import './styles/Index.css';
import ArrowDown from '../icons/arrow-down-icon.svg';

class Index extends React.Component {
  
  render() {
    return <div className="Main">
        <div className={this.props.prevProfile ? 'Me__profile Me__profile--active' : 'Me__profile'}>
          <img className="Me__image" src="https://i.imgur.com/0nl65X2.jpg" alt="Marco Antonio Miranda Sandoval"/>
          <h1 className="Me__full-name">Marco Antonio Miranda Sandoval</h1>
          <div className='Me__skills-list'>
            <span className='Me__skill'>Frontend</span>
            <span className='Me__skill'>UI Design</span>
          </div>
          <div 
            className='Me__leave-button' 
            title="Hide profile"
            onClick={this.props.changeProfileState}  
          >
            <img className='Me__icon' src={ArrowDown} alt="arrow down icon"/>
          </div>
        </div>
    </div>
  }
}

export default Index;